@-webkit-keyframes animate-stripes {
  100% { background-position: -100px 0px; }
}

@keyframes animate-stripes {
  100% { background-position: -100px 0px; }
}

$section-color: #BBC3CD;
#about{
  z-index: 0;
  //background-image: linear-gradient(-245deg, rgba($color-light-grey,.8) 50%,$color-primary 50%);
  border-bottom: 1px solid $color-light-grey;
  padding-top: 0;
  .about-content {
    width: 60%;
    margin: 0 auto;
    @include shadow-1();
    position: relative;
    margin-top: -10rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    background: white;
  }
  background:  url("../../img/geometric.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
  //background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
  .message-me {
    .btn-circle {
      position: absolute;
      top: 0;
      left: 15%;
      display: inline-block;
      background-color: $color-danger;
      width: 3.5rem;
      height: 3.5rem;
      line-height: 3.3rem;
      border-radius: 100%;
      text-align: center;
      @include shadow-1();
      transform: translateY(-50%);
      @include transition(all,$standart-animaion-time,ease-in-out);
      img{
        width: 60%;
      }
      &:hover{
        @include shadow-2();
      }

    }
  }

  .corner{
    display: none;
    position: absolute;
    width: 6rem;
    opacity: .6;
    &-1{
      left: 0;
      bottom: 0;
    }
    &-2{
      right: 0;
      top: 0;
    }
  }

  &::after{
    //content: '';
    width: 100vw;
    height: 40vh;
    position: absolute;
    bottom: 0;
    left: -20vw;

    background-size: contain;

    z-index: 0;

  }


  h1{

  }
  .container{
    z-index: 1;
  }
  h4{
    margin-top: 0.3rem;
  }
  p {

  }
  .webdesign{
    text-align: center;
    margin-top: 4rem;
    &-svg {
      max-width: 100%;
      width: 30%;
      margin-top: 2rem;
    }
  }




  .caaan-do{
    margin-top: 3rem;
    h4{
      text-align: center;
      margin-bottom:  4rem;
    }

    h6{
      text-align: center;
    }

    progress[value]{
      -webkit-appearance: none;
      appearance: none;
      width: 100%;
      &::-webkit-progress-bar {
        background-color: #eee;
        border-radius: 2px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
      }
      &::-webkit-progress-value {
        background-color: $color-danger;
        -webkit-animation: animate-stripes 5s linear infinite;
        animation: animate-stripes 5s linear infinite;
      }
    }
  }


}



