#contact{

  //background: rgba($color-secondary-dark,.4) url("../../img/macbook-work.jpg");
  background: $color-secondary;
  background-image: linear-gradient(120deg, $color-secondary-dark 100%, $color-secondary 0%);
  background-size: cover;
  //background-blend-mode: multiply;
  h2{
    color: $color-primary;
    opacity: .9;
  }
  .card{
    margin: 0 auto;
    background-color: rgba(255,255,255,.2);
    background-color: rgba(0,0,0,.2);
    padding: 2rem;
    //box-shadow:  0px 2px 10px 0px rgba(255,255,255,255.16);
  }

  form{
    background: rgba(white, .1);
    padding: 3rem;
    .form-group{
      position: relative;

      label {
        color: #7AAEF2;
        font-weight: 400;
        opacity: 1;
      }
      &:before{
        //content: "{";
        color: white;
        font-size: 1.5rem;
        width: 2px;
        position: absolute;
        left: 0;
        top: .3rem;
       // background-color: white;
        opacity: .4;
       // transform: rotate(45deg);
      }
      &:after{
        //content: "}";
        color: white;
        font-size: 1.5rem;
        width: 2px;
        position: absolute;
        right: 0;
        bottom: .3rem;
       // background-color: white;
        opacity: .4;
       // transform: rotate(45deg);
      }
      .line{
        height: 1px;
        background-color: $color-primary;
        opacity: .4;
        @include transition(all,.3s, ease-in-out);
        width: 100%;
       // margin-left: 1.9rem ;
      }
    }
    .form-control {
      border: none;
    }

    input[type="text"],textarea{
      background-color: transparent;
      color: white;
     // letter-spacing: 1px;
      font-size: 1rem;
     // margin-left: 1rem;
      font-weight: 300;
      //font-style: italic;
      @include transition(all,.3s);
      @include placeholder {
        color: white;
      }

      &:focus {
        outline: none;
        border-color: transparent;
       // border-color: darken($color-primary,10%);
        //border-width: 2px;
      }
      &:focus+.line{
        opacity: 1;
      }
    }
    .btn-block {
      position: relative;
      .btn {
        position: absolute;
        bottom: 0;
        min-width: 8rem;
        max-width: 100%;
        margin-bottom: 1.5rem;
      }
    }
    textarea{

    }

    input[type="submit"]{

    }
  }

}



