html{
  font-size: $html-font-size-lg;
}

body {
  width: 100%;
  height: 100%;
  // font-family: 'Oxygen', sans-serif;
  //font-family: 'Slabo 27px', serif;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 0.6px;
  line-height: 1.8rem;
  position: relative;
  margin: 0;
  overflow: hidden;
  &.loaded{
    overflow: auto;
  }
}

h1,h2,h3,h4,h5,h6{
  font-family: 'Montserrat', sans-serif;
}

p{
  color: $color-grey;
  font-size: 1rem;
  font-weight: 300;
}


.angle{
  @include angle(after);
}

section{
  position: relative;
  padding: 4rem 0%;
  .header-section{
    position: relative;
    margin-top: 4rem;
    margin-bottom: 4rem;
    .line{
      position: absolute;
      left: 15%;
      top: 20%;
      //margin: 0 auto;
      background-color: $color-primary;
      border: none;
      //border: 1px solid darken($color-light-grey, 2%);
      height: .05rem;
      width: 7rem;
      opacity: .5;

    }
    h1,h2{
      position: relative;
      font-size: 2.827rem;
      padding-bottom: 1rem;
      opacity: .95;
      text-align: center;
      margin-bottom: 0;
      color: $color-dark;
      opacity: .8;
      z-index: 10;
      font-weight: 700;
    }
    h3.secondary{
      font-size: 3rem;
      color: $color-grey;
      position: relative;
      text-align: center;
      display: none;
      left: 0;
      margin: 0 auto;
      top: -4rem;
      z-index: 10;
    }
  }

}
.btn{
  @include transition(all,$standart-animaion-time);
}
.btn.btn-primary{
  color: white !important;
 // border-radius: 30px;
  font-weight: 300;
  border: 1px solid $color-danger;
  background-color: transparent;
  border-radius: 0;
  padding: 0.8rem 3rem;
  // background-color: rgba($color-danger,1) ;
  //background-color: lighten($color-danger, 0%);
  position: relative;

  &:hover{
    background-color: $color-danger;
  }
  &:before{
    //border: $color-danger solid 6px;
    //border-radius: 30px;
  }
}


